<template>
  <div class="pop">
    <v-dialog v-model="dialog">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-1" large dark v-on="on" @click="enterAdd">mdi-plus-circle</v-icon>
      </template>

      <v-card>
        <v-card-title :class="titleClass" primary-title>
          {{title}}
        </v-card-title>

        <v-card-text>
          <v-form ref="form">
            <v-row>
              <v-col cols="12" sm="3">
                <v-text-field label="" v-model="list.date" type="date" :rules="emptyRules"></v-text-field>
              </v-col>
              <v-col cols="12" sm="3" v-if="process=='edit'">
                <v-text-field label="" v-model="list.start_day" type="date" :rules="emptyRules"></v-text-field>
              </v-col>
              <v-col cols="12" sm="3" v-if="process=='edit'">
                <v-text-field label="" v-model="list.end_day" type="date" :rules="emptyRules"></v-text-field>
              </v-col>
              <v-col cols="12" sm="3" v-if="process=='edit'"></v-col>
              <v-col cols="3" v-if="process=='edit'">
                <v-text-field label="" v-model="list.name" outlined readonly></v-text-field>
              </v-col>
              <v-col cols="3" v-if="process=='edit'">
                <v-text-field label="" v-model="list.kind" outlined readonly></v-text-field>
              </v-col>
              <v-col cols="3" v-if="process=='edit'">
                <v-text-field label="" v-model="list.price" :rules="emptyRules"></v-text-field>
              </v-col>

              <v-col cols="12" sm="3">
                <v-text-field label="" v-model="list.quantity" :rules="emptyRules"></v-text-field>
              </v-col>
              <v-col cols="10" class="d-flex align-center" v-if="process=='add'">
                <v-autocomplete
                  label="()"
                  prepend-icon="mdi-cash-usd-outline"
                  v-model="list.feeArray"
                  :items="feeItems"
                  multiple
                  return-object
                ></v-autocomplete>
                <FeeList @getFeeData="getFeeData"></FeeList>
              </v-col>
              <v-col cols="2">
                <v-checkbox label="" v-model="newUser" value="true" hide-details v-if="process=='add'"></v-checkbox>
              </v-col>

              <v-col cols="12">
                <v-text-field label="" v-model="list.note"></v-text-field>
              </v-col>
            </v-row>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="error" @click="addOK" v-if="process=='add'"></v-btn>
          <v-btn class="success" @click="editOK" v-if="process=='edit'"></v-btn>
        </v-card-actions>
        <!-- <pre>{{ list.feeArray }}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import dayjs from "dayjs";
import FeeList from "@/components/FeeList";

export default {
  components: { FeeList },
  data() {
    return {
      dialog: false,
      list: {}, //
      oldList: {}, //
      emptyRules: [(v) => !!v || ""],
      title: "", //
      titleClass: "", //
      process: "", //
      nowDay: dayjs().format("YYYY-MM-DD"),
      firstDay: dayjs().startOf("month").format("YYYY-MM-DD"), //
      lastDay: dayjs().endOf("month").format("YYYY-MM-DD"), //
      feeItems: [],
      newUser: false,
    };
  },

  updated() {
    this.nowDay = dayjs().format("YYYY-MM-DD"); //
  },

  mounted() {
    //function (showFromParent) id
    this.$on("childFn", function (item) {
      this.dialog = true;
      this.enterEdit(item);
    });

    this.getFeeData();
  },

  methods: {
    enterAdd() {
      //
      this.process = "add";
      this.title = "";
      this.titleClass = "font-weight-black error lighten-2";
      this.list = {
        date: dayjs().format("YYYY-MM-DD"),
        start_day: dayjs().startOf("month").format("YYYY-MM-DD"), //
        end_day: dayjs().endOf("month").format("YYYY-MM-DD"), //
        quantity: 1,
        feeArray: [],
      };
      
    },
    enterEdit(item) {
      //
      this.process = "edit";
      this.title = "";
      this.titleClass = "font-weight-black success lighten-2";
      this.oldList = item; //
      this.list = { ...item }; //
      
    },
    addOK() {
      //
      if (this.list.feeArray.length == 0) {
        var pop = { msg: "!!", type: true, theme: "warning" };
        this.$store.commit("snackbar", pop);
        return false;
      }

      var postData = {
        data: this.list,
        newUser : this.newUser,
      }
      if (this.$refs.form.validate()) {
        this.$emit("finishAdd", postData);
        this.dialog = false;
      }
    },
    editOK() {
      //
      if (this.$refs.form.validate()) {
        for (var i in this.list) {
          //,vue
          this.oldList[i] = this.list[i];
        }
        this.$emit("finishEdit", this.list);
        this.dialog = false;
      }
    },
    getFeeData() {
      //this.$store.commit("setLoadingOn"); //on
      //
      this.axios
        .post("general/getAll/" + this.$store.state.databaseName + "/fee")
        .then((rs) => {
          // console.log(rs.data)
          var newData = rs.data.map((item) => {
            return {
              snkey: item.snkey,
              text: `[${item.name}:${item.kind}:$${item.price}]`,
              name: item.name,
              kind: item.kind,
              price: item.price,
            };
          });

          // KIND -> PRICE
          newData = newData.sort(function(a,b){
            return a.kind < b.kind ? 1 : -1;
          });
          this.feeItems = newData.sort(function(a,b){
            if (a.kind == b.kind){
              return parseInt(a.price) < parseInt(b.price) ? 1 : -1;
            }
          });
          //end

          // this.$store.commit("setLoadingOff"); //off
        });
    },
  },
};
</script>

