<template>
  <div class="accountingsearch">
    <v-dialog v-model="dialog">
      <accountingadd @finishEdit="finishEdit" ref="enterEdit" v-show="false"></accountingadd>
      <!-- <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="enterSearch">mdi-plus-circle</v-icon>
      </template>-->

      <v-form ref="form" class="mt-3">
        <v-card>
          <v-card-title :class="titleClass" primary-title>{{title}}</v-card-title>

          <v-card-text>
            <v-row>
              <v-col cols="8">
                <v-toolbar class="mt-2" dense>
                  <v-text-field
                    hide-details
                    prepend-icon="mdi-magnify"
                    single-line
                    label=""
                    v-model="serachKey"
                  ></v-text-field>
                  
                </v-toolbar>
              </v-col>
              <v-col cols="4">
                <v-toolbar class="mt-2" dense>
                  <v-text-field
                    hide-details
                    prepend-icon="mdi-table-eye-off"
                    single-line
                    label=""
                    v-model="disViewKey"
                  ></v-text-field>
                </v-toolbar>
              </v-col>
            </v-row>

            <v-card class="mt-2">
              <v-card-text>
                <v-data-iterator
                  :items="searchfilter"
                  :items-per-page.sync="itemsPerPage"
                  :footer-props="{ itemsPerPageOptions }"
                >
                  <template v-slot:default="props">
                    <v-simple-table fixed-header class="mt-2 text-no-wrap">
                      <template v-slot:default>
                        <thead class="title">
                          <tr>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr v-for="(item,index) in props.items" :key="index">
                            <td>
                              <v-menu transition="scale-transition" offset-y>
                                <template v-slot:activator="{ on }">
                                  <v-btn color="primary" dark v-on="on"></v-btn>
                                </template>
                                <v-list>
                                  <v-list-item @click="enterEdit(item)">
                                    <v-list-item-title></v-list-item-title>
                                  </v-list-item>
                                  <v-list-item @click="finishDel(item)">
                                    <v-list-item-title></v-list-item-title>
                                  </v-list-item>
                                </v-list>
                              </v-menu>
                            </td>
                            <td>{{item.user_p_code}}</td>
                            <td>{{item.user_name}}</td>
                            <td>{{item.bed_name}}</td>
                            <td>{{item.date}}</td>
                            <td>{{item.start_day}}</td>
                            <td>{{item.end_day}}</td>
                            <td>{{item.name}}</td>
                            <td>{{item.price}}</td>
                            <td>{{item.quantity}}</td>
                            <td>{{item.price * item.quantity}}</td>
                            <td>{{item.kind}}</td>
                            <td>{{item.note}}</td>
                            <td>{{item.create_man}}</td>
                            <td>{{item.edit_man}}</td>
                          </tr>
                        </tbody>
                      </template>
                    </v-simple-table>
                  </template>
                </v-data-iterator>
              </v-card-text>
            </v-card>
          </v-card-text>

          <v-divider></v-divider>

          <v-card-actions>
            <v-spacer></v-spacer>
            <!-- <v-btn class="error" @click="addOK" v-if="process=='add'"></v-btn>
            <v-btn class="success" @click="editOK" v-if="process=='edit'"></v-btn>-->
          </v-card-actions>
          <!-- <pre>{{items}}</pre> -->
        </v-card>
      </v-form>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";
import dayjs from "dayjs";
import accountingadd from "@/views/main/AccountingAdd";

export default {
  components: { accountingadd },
  props: ["useritems"],
  data() {
    return {
      dialog: false,
      list: {},
      emptyRules: [(v) => !!v || ""],
      title: "", //
      titleClass: "", //
      items: [],
      serachKey: "",
      disViewKey:"",
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10,
    };
  },



  computed: {
    searchfilter() {
      if (this.items.length > 0) {
        var keys = this.serachKey.split(" ");
        var str = "";

        var matchData = keys.reduce(function (prev, element) {
          return prev.filter((item) => {
            str = JSON.stringify(item).toUpperCase();
            return str.includes(element.toUpperCase())
          });
        }, this.items);
        
        if (this.disViewKey.length > 0){
          var diskeys = this.disViewKey.split(" ");
          matchData = diskeys.reduce(function (prev, element) {
          return prev.filter((item) => {
            str = JSON.stringify(item).toUpperCase();
            return !str.includes(element.toUpperCase());
          });
        }, matchData);
        }

        matchData =  matchData.sort(function(a,b){
          return parseInt(a.snkey) < parseInt(b.snkey) ? 1 : -1;
        })

        return matchData;
      }
    },
  },

  mounted() {
    //function (showFromParent) id
    this.$on("childFn", function (item) {
      this.dialog = true;
      this.enterProcess(item);
    });
  },

  methods: {
    //
    enterProcess() {
      this.title = "";
      this.titleClass = "font-weight-black warning lighten-2";
      this.list = {};

      this.getAllData();
    },
    getAllData() {
      //this.$store.commit("setLoadingOn"); //on
      this.axios
        .post("accounting/getall/" + this.$store.state.databaseName)
        .then((rs) => {
          this.items = rs.data.map((i) => {
            var matchData = this.useritems.filter((uitem) => {
              return i.user_snkey == uitem.snkey;
            });

            if (matchData.length > 0) {
              //
              i.user_p_code = matchData[0].p_code;
              i.user_name = matchData[0].name;
              i.bed_name = matchData[0].bed_name;
            }
            // console.log(i)
            return i;
          });

          // this.sortBy();
          // this.$store.commit("setLoadingOff"); //off
        });
    },
    enterEdit(item) {
      // -> 
      this.$refs.enterEdit.$emit("childFn", item);
    },
    finishEdit(item) {
      //(popupadd,,itemlist)
      this.$store.commit("getTime"); //
      var postData = {
        date: item.date,
        snkey: item.snkey,
        start_day: item.start_day,
        end_day: item.end_day,
        quantity: item.quantity,
        price: item.price,
        note: item.note,
        edit_man:
          this.$store.state.pData.username + "(" + this.$store.state.time + ")", //,,
      };

      this.axios
        .post(
          "general/edit/" + this.$store.state.databaseName + "/accounting",
          qs.stringify(postData)
        )
        .then((rs) => {
          if (rs.data.state == 1) {
            var pop = { msg: "", type: true, theme: "success" };
            this.$store.commit("snackbar", pop);
            //this.getAccounting();
            // this.computeMonthTotal();
          }
        });
    },
    finishDel(delData) {
      //
      this.$confirm("?").then((res) => {
        if (res) {
          delData.delman =
            this.$store.state.pData.username +
            "(" +
            dayjs().format("YYYY-MM-DD HH:mm:ss") +
            ")";
          var postData = {
            snkey: delData.snkey,
            tablename: "accounting",
            info: JSON.stringify(delData),
          };

          this.axios
            .post(
              "general/delv3/" + this.$store.state.databaseName + "/accounting",
              qs.stringify(postData)
            )
            .then((rs) => {
              // console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.getAllData();
              }
            });
        }
      });
    },
  },
};
</script>


<style lang="scss" scoped>
h2 {
  span {
    vertical-align: middle;
  }
}
thead {
  th {
    font-size: 1.2rem;
    font-family: "";
    color: red;
  }
}
tbody {
  td {
    font-size: 1rem;
  }
}
</style>