<template>
  <div class="pop">
    <v-dialog v-model="dialog">
      <!-- <template v-slot:activator="{ on }">
        <v-icon color="red lighten-1" large dark v-on="on" @click="enterAdd">mdi-plus-circle</v-icon>
      </template> -->

      <v-card>
        <v-card-title :class="titleClass" primary-title>{{title}}</v-card-title>

        <v-card-text>
          <v-form ref="form">
            <v-row>
              <v-col cols="12" sm="3">
                <v-text-field label="" v-model="list.month" outlined readonly></v-text-field>
              </v-col>
              <v-col cols="12" sm="3">
                <v-text-field label="" v-model="list.total_price" outlined readonly></v-text-field>
              </v-col>
              <v-col cols="12" sm="3">
                <v-text-field label="" v-model="total_pay"  outlined readonly></v-text-field>
              </v-col>
              <v-col cols="12" sm="3">
                <v-text-field label="" :value="total_pay - list.total_price"  outlined readonly></v-text-field>
              </v-col>
              <v-col cols="12" sm="3">
                <v-text-field label="" v-model="list.paid_date" type="date" 
                append-outer-icon="mdi-calendar-star"
                @click:append-outer="list.paid_date = nowDay"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="3">
                <v-text-field label="()" v-model="list.paid_price_cash"></v-text-field>
              </v-col>
              <v-col cols="12" sm="3">
                <v-text-field label="()" v-model="list.paid_price_check"></v-text-field>
              </v-col>
              <v-col cols="12" sm="3">
                <v-text-field label="()" v-model="list.paid_price_remit"></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field label="" v-model="list.note"></v-text-field>
              </v-col>
            </v-row>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="error" @click="addOK" v-if="process=='add'"></v-btn>
          <v-btn class="success" @click="editOK" v-if="process=='edit'"></v-btn>
        </v-card-actions>
        <!-- <pre>{{list}}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import dayjs from "dayjs";
import FeeList from "@/components/FeeList";

export default {
    components: { FeeList },
  data() {
    return {
      dialog: false,
      list: {}, //
      oldList: {}, //
      emptyRules: [v => !!v || ""],
      title: "", //
      titleClass: "", //
      process: "", //
      nowDay:dayjs().format("YYYY-MM-DD"),
      firstDay:dayjs().startOf('month').format("YYYY-MM-DD"), //
      lastDay:dayjs().endOf('month').format("YYYY-MM-DD"), //
      feeItems:[],
    };
  },

  computed:{
    total_pay(){
      var total = parseInt(this.list.paid_price_cash) + parseInt(this.list.paid_price_check) + parseInt(this.list.paid_price_remit)
      return this.list.paid_price = total;
    }
  },

  updated() {
    this.nowDay = dayjs().format("YYYY-MM-DD"); //
  },

  mounted() {
    //function (showFromParent) id
    this.$on("childFn", function(item) {
      this.dialog = true;
      this.enterEdit(item);
    });

    // this.getFeeData();
  },

  methods: {
    // enterAdd() {
    //   //()
    //   this.process = "add";
    //   this.title = "";
    //   this.titleClass = "font-weight-black error lighten-2";
    //   this.list = {
    //     date:dayjs().format("YYYY-MM-DD"),
    //     start_day:dayjs().startOf('month').format("YYYY-MM-DD"), //
    //     end_day:dayjs().endOf('month').format("YYYY-MM-DD"), //
    //     quantity:1,
    //     feeArray:[],
    //   };

    // },
    enterEdit(item) {
      //
      this.process = "edit";
      this.title = "";
      this.titleClass = "font-weight-black success lighten-2";
      this.oldList = item; //
      this.list = { ...item }; //
      
    },
    // addOK() {
    //   //
    //   if (this.list.feeArray.length == 0){
    //     var pop = { msg: "!!", type: true, theme: "warning" };
    //     this.$store.commit("snackbar", pop);
    //     return false;
    //   }

    //   if (this.$refs.form.validate()) {
    //     this.$emit("finishAdd", this.list);
    //     this.dialog = false;
    //   }
    // },
    editOK() {
      //
      if (this.$refs.form.validate()) {
        for (var i in this.list) {
          //,vue
          this.oldList[i] = this.list[i];
        }
        this.$emit("finishEditPayment", this.list);
        this.dialog = false;
      }
    },
   
  }
};
</script>

