<template>
  <div class="accountinglist">
    <v-btn class="slideBtn" color="primary" @click.stop="changecolset">
      <v-icon>mdi-badge-account-horizontal</v-icon>
    </v-btn>
    <accountingsearch :useritems="items" ref="enterSearch"></accountingsearch>
    <paymentsearch :useritems="items" ref="enterPaymentSearch"></paymentsearch>

    <v-row>
      <v-col cols="12" md="6">
        <h2 class="subheading grey--text">
          <v-icon class="mx-1">mdi-cash-usd-outline</v-icon>
          <span></span>
        </h2>
      </v-col>
      <v-col cols="12" md="6" class="text-right d-flex align-center">
        <v-row>
          <v-col cols="8">
            <!-- <v-btn class="mx-1 primary" @click.stop="cloneFee">{{nowmonth}}</v-btn> -->
          </v-col>
          <v-col cols="4">
            <v-text-field
              class="mx-1 my-0 py-0"
              label=""
              type="month"
              v-model="nowmonth"
              hide-details
            ></v-text-field>
          </v-col>
        </v-row>
        <!--  -->
        <v-menu transition="scale-transition" offset-y>
          <template v-slot:activator="{ on }">
            <v-btn color="primary" dark v-on="on"></v-btn>
          </template>
          <v-list>
            <v-list-item>
              <v-list-item-title>
                <v-btn
                  class="warning"
                  @click.stop="$refs.enterPaymentSearch.$emit('childFn')"
                >
                  
                  <v-icon class="mx-2">mdi-text-box-search</v-icon>
                </v-btn>
              </v-list-item-title>
            </v-list-item>
            <v-list-item>
              <v-list-item-title>
                <v-btn
                  class="warning"
                  @click.stop="$refs.enterSearch.$emit('childFn')"
                >
                  
                  <v-icon class="mx-2">mdi-text-box-search</v-icon>
                </v-btn>
              </v-list-item-title>
            </v-list-item>
            <v-list-item>
              <v-list-item-title>
                <v-btn class="primary" @click.stop="goPrint">
                  
                  <v-icon class="mx-2">mdi-printer</v-icon>
                </v-btn>
              </v-list-item-title>
            </v-list-item>
            <v-list-item>
              <v-list-item-title>
                <v-btn class="success" @click.stop="printList">
                  {{ nowmonth }} 
                  <v-icon class="mx-2" small
                    >mdi-format-list-bulleted-square</v-icon
                  >
                </v-btn>
              </v-list-item-title>
            </v-list-item>
            <v-list-item>
              <v-list-item-title>
                <v-btn class="success" @click.stop="cloneFee">
                  {{ nowmonth }} 
                  <v-icon class="mx-2">mdi-content-copy</v-icon>
                </v-btn>
              </v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </v-col>
    </v-row>

    <v-card>
      <v-card-title>
        <v-spacer></v-spacer>
      </v-card-title>

      <v-card-text>
        <v-row>
          <transition name="fadeLeft">
            <v-col
              cols="colset"
              v-if="colset"
              style="animation-fadeupbig: 0.1s"
            >
              <v-card>
                <v-card-title class="primary lighten-2 white--text">
                  
                  <v-spacer></v-spacer>
                </v-card-title>
                <v-card-text class="mt-2">
                  <v-toolbar dense>
                    <v-text-field
                      hide-details
                      prepend-icon="mdi-magnify"
                      single-line
                      label=""
                      v-model="serachKey"
                    ></v-text-field>
                  </v-toolbar>

                  <div style="overflow: auto">
                    <v-list class="mt-2" height="60vh">
                      <v-list-item-group v-model="model" color="primary">
                        <v-list-item
                          v-for="(item, i) in searchfilter"
                          :key="i"
                          @click="getUserAccounting(item)"
                        >
                          <v-list-item-icon class="mr-2">
                            <v-icon>mdi-account</v-icon>
                          </v-list-item-icon>
                          <v-list-item-content>
                            <v-list-item-title
                              >{{ item.name }} :{{
                                item.bed_name
                              }}</v-list-item-title
                            >
                            <v-list-item-subtitle
                              >:{{ item.p_code }}</v-list-item-subtitle
                            >
                          </v-list-item-content>
                        </v-list-item>
                      </v-list-item-group>
                    </v-list>
                  </div>
                </v-card-text>
              </v-card>
            </v-col>
          </transition>

          <v-col :cols="12 - colset">
            <v-card>
              <v-card-title class="primary lighten-2 white--text">
                {{ selectUser.name }} :{{ selectUser.p_code }} :{{
                  selectUser.bed_name
                }}
                <v-spacer></v-spacer>
                <v-alert
                  class="my-0 mx-2"
                  v-if="Object.keys(selectUser).length"
                >
                  <v-row>
                    <v-text-field
                      label=""
                      type="date"
                      class="mx-1"
                      hide-details
                      v-model="selectUser.out_date"
                    ></v-text-field>
                    <v-btn
                      class="error align-self-center"
                      @click.stop="leaveOut"
                      ></v-btn
                    >
                  </v-row>
                </v-alert>
                <v-alert class="ma-0" v-if="Object.keys(selectUser).length">
                  <v-row>
                    <v-text-field
                      label=""
                      type="date"
                      class="mx-1"
                      hide-details
                      v-model="selectUser.margin_date"
                    ></v-text-field>
                    <v-text-field
                      label=""
                      hide-details
                      v-model="selectUser.margin"
                    ></v-text-field>
                    <v-btn
                      class="primary align-self-center"
                      @click.stop="updateUser"
                      ></v-btn
                    >
                  </v-row>
                </v-alert>
              </v-card-title>

              <v-card-text>
                <div class="d-flex">
                  
                  <v-spacer></v-spacer>
                  :{{ nopaid }}
                </div>
                <v-simple-table fixed-header class="text-no-wrap" height="200px">
                  <template v-slot:default>
                    <thead>
                      <tr>
                        <th class="text-left"></th>
                        <th class="text-left"></th>
                        <th class="text-left"></th>
                        <th class="text-left"></th>
                        <!-- <th class="text-left" rowspan="2"></th> -->
                        <th class="text-left"></th>
                        <th class="text-left"></th>
                      </tr>
                      <!-- <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                      </tr>-->
                    </thead>
                    <tbody v-for="(item, index) in paymentItems" :key="index">
                      <tr @click.stop="nowmonth = item.month">
                        <td>
                          <v-icon
                            color="success"
                            @click.stop="
                              $refs.enterEditPayment.$emit('childFn', item)
                            "
                            >mdi-pencil</v-icon
                          >
                        </td>
                        <td>{{ item.month }}</td>
                        <td>{{ item.total_price | filtermoney }}</td>
                        <td>{{ item.paid_price | filtermoney }}</td>
                        <!-- <td>{{ item.paid_price_cash }}</td>
                        <td>{{ item.paid_price_check }}</td>
                        <td>{{ item.paid_price_remit }}</td>-->
                        <!-- <td>{{ item.paid_date }}</td> -->
                        <td>
                          {{
                            (item.paid_price - item.total_price) | filtermoney
                          }}
                        </td>
                        <td>{{ item.note }}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
                <PaymentAdd @finishEditPayment="finishEditPayment" ref="enterEditPayment" />
              </v-card-text>
            </v-card>

            <v-card class="mt-2">
              <v-card-title class="primary lighten-2 white--text">
                {{ nowmonth }} - : {{ monthfilter.length }}
                <AccountingAdd
                  class="ml-3"
                  @finishAdd="finishAdd"
                  @finishEdit="finishEdit"
                  ref="enterEdit"
                  v-if="Object.keys(selectUser).length"
                ></AccountingAdd>
                <v-spacer></v-spacer>
                <v-alert class="my-0" v-if="Object.keys(selectUser).length">
                  <v-row>
                    <v-select
                      label=""
                      :items="namefilter"
                      v-model="selectedName"
                      hide-details
                    >
                    </v-select>
                    <v-text-field
                      label=""
                      class="mx-1"
                      v-model="setPrice"
                      hide-details
                    ></v-text-field>
                    <v-btn
                      class="primary align-self-center"
                      @click.stop="setSameKindPrice"
                      ></v-btn
                    >
                    <v-checkbox
                      class="my-0 ml-2 pa-2 primary alertzone align-self-center"
                      label=""
                      color="white"
                      v-model="selectUser.promise_fee"
                      value="true"
                      @change="updateUser"
                      v-if="Object.keys(selectUser).length"
                      hide-details
                      style="border-radius: 4px"
                    ></v-checkbox>
                  </v-row>
                </v-alert>
              </v-card-title>
              <v-card-text>
                <v-simple-table fixed-header class="text-no-wrap">
                  <template v-slot:default>
                    <thead>
                      <tr>
                        <th class="text-left"></th>
                        <th class="text-left"></th>
                        <th class="text-left"></th>
                        <th class="text-left"></th>
                        <th class="text-left"></th>

                        <th class="text-left"></th>
                        <th class="text-left"></th>
                        <th class="text-left"></th>

                        <th class="text-left"></th>
                        <th class="text-left"></th>
                        <th class="text-left"></th>
                        <th class="text-left"></th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr
                        v-for="(item, index) in monthfilter"
                        :key="item.snkey"
                      >
                        <td>
                          <v-icon color="green" @click.stop="enterEdit(item)"
                            >mdi-pencil</v-icon
                          >
                          <v-icon color="red" @click.stop="finishDel(item)"
                            >mdi-close</v-icon
                          >
                          {{ index + 1 }}.
                        </td>
                        <td>{{ item.date }}</td>
                        <td>{{ item.start_day }}</td>
                        <td>{{ item.end_day }}</td>
                        <td>{{ item.name }}</td>

                        <td>{{ item.price | filtermoney }}</td>
                        <td>{{ item.quantity }}</td>
                        <td>
                          {{ (item.price * item.quantity) | filtermoney }}
                        </td>

                        <td>{{ item.kind }}</td>
                        <td>{{ item.note }}</td>
                        <td>{{ item.create_man }}</td>
                        <td>{{ item.edit_man }}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>

    <!-- <pre class="d-flex justify-center"> {{ monthfilter }} </pre> -->
  </div>
</template>

<script>
import qs from "qs";
import feelist from "@/components/FeeList";
import AccountingAdd from "@/views/main/AccountingAdd";
import PaymentAdd from "@/views/main/PaymentAdd";
import accountingsearch from "@/views/main/AccountingSearch";
import paymentsearch from "@/views/main/PaymentSearch";
import dayjs from "dayjs";
require("vue2-animate/dist/vue2-animate.min.css");
//animate 
// import animated from 'vue2-animate.min.css' // npm install animate.css --save

export default {
  components: {
    feelist,
    AccountingAdd,
    PaymentAdd,
    accountingsearch,
    paymentsearch,
  },
  data() {
    return {
      items: [], //
      serachKey: "", //
      model: "", //list
      selectUser: [], //
      nowday: dayjs().format("YYYY-MM-DD"), //
      nowmonth: dayjs().format("YYYY-MM"), //
      accountingItems: [], //
      paymentItems: [], //
      colset: 3,
      //,
      matchListAccountings: [],
      matchListPayments: [],
      //
      selectedName: "",
      setPrice: 0,
    };
  },

  filters: {
    filtermoney(num) {
      const n = Number(num);
      return `$${n.toFixed(0).replace(/./g, (c, i, a) => {
        const currency =
          i && c !== "." && (a.length - i) % 3 === 0
            ? `, ${c}`.replace(/\s/g, "")
            : c;
        return currency;
      })}`;
    },
  },

  computed: {
    namefilter() {
      this.selectedName = "";
      var newArr = new Set();
      this.monthfilter.forEach((i) => {
        newArr.add(i["name"]);
      });
      return [...newArr];
    },
    searchfilter() {
      var keys = this.serachKey.split(" ");
      var str = "";

      return keys.reduce(function (prev, element) {
        return prev.filter((item) => {
          str = JSON.stringify(item).toUpperCase();
          if (str.includes(element.toUpperCase())) {
            return item;
          }
        });
      }, this.items);
    },
    monthfilter() {
      //
      // console.log("");
      var matchData = this.accountingItems.filter((item) => {
        return item.date.includes(this.nowmonth);
      });

      // console.log(matchData);

      //1.
      matchData = matchData.sort((a, b) => {
        return parseInt(a.price) < parseInt(b.price) ? 1 : -1;
      });

      matchData = matchData.sort((a, b) => {
        if (a.price == b.price) {
          return a.name < b.name ? 1 : -1;
        }
      });

      return matchData.sort((a, b) => {
        if (a.name == b.name) {
          return a.date > b.date ? 1 : -1;
        }
      });
    },
    nopaid() {
      //
      return this.paymentItems.reduce((total, i) => {
        return (
          parseInt(total) + (parseInt(i.total_price) - parseInt(i.paid_price))
        );
      }, 0);
    },
  },

  watch: {
    monthfilter(val) {
      //5~9
      var month = dayjs(this.nowmonth).format("MM");
      if (month >= 5 && month <= 9) {
        var rs = val.some((i) => {
          return i.fee_snkey == "159"; //-1 snkey
        });

        if (!rs && val.length && !this.selectUser.promise_fee) {
          // alert('->');
          this.$confirm("-1,?").then(
            (res) => {
              if (res) {
                var feeItem = {
                  //
                  date: dayjs(this.nowmonth).format("YYYY-MM-01"),
                  quantity: 1,
                  feeArray: [
                    {
                      snkey: "159",
                      price: "500",
                      kind: "-1",
                    },
                  ],
                };

                this.finishAdd(feeItem);
              }
            }
          );
        }
      }
    },
  },

  mounted() {
    this.auth = this.$store.state.pData;
    this.getAllData();
  },

  methods: {
    //
    setSameKindPrice() {
      if (this.selectedName.length == 0) {
        var pop = { msg: "", type: true, theme: "warning" };
        this.$store.commit("snackbar", pop);
        return false;
      }

      this.$confirm(
        " [" + this.selectedName + "] : $" + this.setPrice
      ).then((res) => {
        if (res) {
          this.$store.commit("getTime"); //
          let matchData = this.monthfilter.filter((i) => {
            if (i.name === this.selectedName) {
              i.price = this.setPrice;
              i.edit_man =
                this.$store.state.pData.username +
                "(" +
                this.$store.state.time +
                ")" +
                i.edit_man; //,,
              return i;
            }
          });

          let postData = matchData.map((i) => {
            let newItem = { ...i };
            delete newItem["name"];
            delete newItem["kind"];
            return newItem;
          });

          this.axios
            .post(
              "general/editMulti/" +
                this.$store.state.databaseName +
                "/accounting",
              qs.stringify(postData)
            )
            .then((rs) => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.computeMonthTotal();
              }
            });
        }
      });
    },
    //
    printList() {
      //
      this.$confirm("?").then((res) => {
        if (res) {
          var printData = {
            month: this.nowmonth,
            users: this.items,
          };

          //
          let routeData = this.$router.resolve({path: "/print/accountinglistprint",query:{data:JSON.stringify(printData)}});
          window.open(routeData.href, "_blank");
        }
      });
    },
    goPrint() {
      this.$router.push("/main/printmanager/accountingprintmanage");
    },
    //
    changecolset() {
      if (this.colset == 3) {
        this.colset = 0;
      } else {
        this.colset = 3;
      }
    },
    //
    cloneFee() {
      this.$confirm(
        "" +
          this.nowmonth +
          "" +
          dayjs(this.nowmonth).add(1, "month").format("YYYY-MM") +
          "?"
      ).then((res) => {
        if (res) {
          //
          //this.$store.commit("setLoadingOn"); //on
          this.axios
            .post(
              "accounting/getAll/" +
                this.$store.state.databaseName +
                "/accounting"
            )
            .then((rs) => {
              var matchData = rs.data.filter((i) => {
                // 1. 2. 3. 4.
                // console.log(i);
                return (
                  dayjs(i.date).format("YYYY-MM") == this.nowmonth &&
                  i.kind.includes("") &&
                  parseInt(i.price) > 0
                );
              });

              //4.
              matchData = matchData.filter((i) => {
                var matchUser = this.items.find((userItem) => {
                  return (
                    userItem.snkey == i.user_snkey &&
                    userItem.bed_name != "" &&
                    userItem.bed_name != null
                  );
                });

                if (matchUser != undefined) {
                  return i;
                }
              });

              this.$store.commit("getTime"); //
              var newItem = {};
              if (matchData.length) {
                var postData = matchData.map((i) => {
                  // newItem = { ...i };
                  i.date = i.start_day = dayjs(i.date)
                    .add(1, "month")
                    .format("YYYY-MM-01");
                  i.end_day = dayjs(i.date).endOf("month").format("YYYY-MM-DD");
                  i.create_man =
                    this.$store.state.pData.username +
                    "(" +
                    this.$store.state.time +
                    ")"; //,,
                  delete i.snkey;
                  delete i.edit_man;
                  delete i.name;
                  delete i.kind;
                  delete i.unit;
                  return i;
                });

                //
                this.axios
                  .post(
                    "general/addMulti/" +
                      this.$store.state.databaseName +
                      "/accounting",
                    qs.stringify(postData)
                  )
                  .then((rs) => {
                    // console.log(rs.data);
                    if (rs.data.state == 1) {
                      var pop = {
                        msg: "",
                        type: true,
                        theme: "success",
                      };
                      this.$store.commit("snackbar", pop);
                      // this.getAccounting();
                    }
                  });
              } else {
                var pop = {
                  msg: ",!!",
                  type: true,
                  theme: "warning",
                };
                this.$store.commit("snackbar", pop);
              }
              // this.$store.commit("setLoadingOff"); //off
            });
        }
      });

      //
      // this.$confirm("?").then(res => {
      //   if (res) {
      //     //
      //     var matchData = this.monthfilter.filter(i => {
      //       return i.kind.includes("");
      //     });

      //     //
      //     this.$store.commit("getTime"); //
      //     var postData = matchData.map(i => {
      //       var newItem = { ...i };
      //       newItem.date = dayjs(newItem.date).add(1, "month").format("YYYY-MM-01");
      //       newItem.start_day = dayjs(newItem.start_day).add(1, "month").format("YYYY-MM-01");
      //       newItem.end_day = dayjs(newItem.end_day).add(1, "month").endOf("month").format("YYYY-MM-DD");
      //       newItem.create_man = this.$store.state.pData.username + "(" + this.$store.state.time +")"; //,,
      //       delete newItem.snkey;
      //       delete newItem.edit_man;
      //       delete newItem.name;
      //       delete newItem.kind;
      //       return newItem;
      //     });

      //     //
      //     this.axios
      //       .post(
      //         "general/addMulti/" + this.$store.state.databaseName + "/accounting",
      //         qs.stringify(postData)
      //       )
      //       .then(rs => {
      //         if (rs.data.state == 1) {
      //           var pop = { msg: "", type: true, theme: "success" };
      //           this.$store.commit("snackbar", pop);
      //           this.getAccounting();
      //         }
      //       });
      //   }
      // });
    },
    //bed
    sortBybedname() {
      this.items = this.items.sort(function (a, b) {
        var a = a.bed_name;
        var b = b.bed_name;

        if (a === b) {
          return 0;
        } else if (a === null) {
          return 1;
        } else if (b === null) {
          return -1;
        } else {
          return a > b ? 1 : -1;
        }
      });
    },
    getAllData() {
      // console.log("");
      //
      //this.$store.commit("setLoadingOn"); //on
      //
      this.axios
        .post("user/getAll/" + this.$store.state.databaseName)
        .then((rs) => {
          this.items = rs.data;
          this.sortBybedname();
          // this.$store.commit("setLoadingOff"); //off
        });
    },
    getAccounting() {
      // console.log("");
      //
      //this.$store.commit("setLoadingOn"); //on
      this.axios
        .post(
          "accounting/getAccountingByUserSnkey/" +
            this.$store.state.databaseName +
            "/" +
            this.selectUser.snkey
          // ,qs.stringify(postData)
        )
        .then((rs) => {
          // console.log(rs.data)
          if (rs.data.length > 0) {
            this.accountingItems = rs.data;
            this.computeMonthTotal();
          } else {
            this.accountingItems = this.paymentItems = [];
          }
          // this.$store.commit("setLoadingOff"); //off
        });
    },
    computeMonthTotal() {
      //
      // console.log("");
      var items = this.accountingItems;
      if (items.length == 0) {
        //
        return false;
      }

      //

      //1.
      var monthArr = items.map((item) => {
        return dayjs(item.date).format("YYYY-MM");
        // return dayjs(item.start_day).format("YYYY-MM");
      });
      //2.
      var uniqArray = new Set(monthArr);
      monthArr = [...uniqArray]; //

      //3.
      var monthGroup = [];
      monthArr.forEach((month) => {
        monthGroup[month] = items.filter((i) => {
          return i.date.includes(month);
          // return i.start_day.includes(month);
        });
      });

      // console.log(monthGroup['2020-05'])
      var monthData = []; //
      //4.()
      monthArr.forEach((item) => {
        var total_price = monthGroup[item].reduce((total, i) => {
          return total + i.price * i.quantity;
        }, 0);

        this.$store.commit("getTime"); //
        monthData.push({
          user_snkey: this.selectUser.snkey,
          month: item,
          total_price: total_price,
          create_man:
            this.$store.state.pData.username +
            "(" +
            this.$store.state.time +
            ")", //,,
        });
      });

      //this.$store.commit("setLoadingOn"); //on
      this.axios
        .post(
          "payment/autoAddEdit/" + this.$store.state.databaseName + "/payment",
          qs.stringify(monthData)
        )
        .then((rs) => {
          // console.log(rs.data)
          this.getPayment();
          // this.$store.commit("setLoadingOff"); //off
        });
    },
    getPayment() {
      //
      // console.log("");
      var postData = {
        key: "user_snkey",
        value: this.selectUser.snkey,
      };
      //this.$store.commit("setLoadingOn"); //on
      //
      this.axios
        .post(
          "general/getByKey/" + this.$store.state.databaseName + "/payment",
          qs.stringify(postData)
        )
        .then((rs) => {
          // console.log(rs.data);
          this.paymentItems = rs.data;
          this.sortBy();
          if (this.paymentItems.length > 0) {
            //>0
            this.checkPayment();
          }
          // this.$store.commit("setLoadingOff"); //off
        });
    },
    checkPayment() {
      //
      // console.log("");
      this.paymentItems.forEach((item) => {
        var counts = this.accountingItems.filter((i) => {
          return i.date.includes(item.month);
          // return i.start_day.includes(item.month);
        });

        if (counts.length == 0) {
          this.axios
            .post(
              "general/del/" + this.$store.state.databaseName + "/payment",
              qs.stringify(item)
            )
            .then((rs) => {
              this.getPayment();
            });
        }
      });
    },
    sortBy() {
      //id
      this.paymentItems = this.paymentItems.sort(function (a, b) {
        return a.month < b.month ? 1 : -1;
      });
      // this.sort = !this.sort;
    },
    getUserAccounting(item) {
      //->
      this.selectUser = item;
      // this.reflashAllAccount();
      this.getAccounting();
      // this.getPayment();
    },
    enterEdit(item) {
      // -> 
      this.$refs.enterEdit.$emit("childFn", item);
    },
    //
    rebuildDay(rsData) {
      var item = rsData.data;

      var postData = [];
      this.$store.commit("getTime"); //

      item.feeArray.forEach((feeitem, index) => {
        // console.log('feeitem',feeitem);
        var firstDay = dayjs(item.date).startOf("month").format("YYYY-MM-DD"); //
        var lastDay = dayjs(item.date).endOf("month").format("YYYY-MM-DD"); //

        //~ 1~, =  = 
        var createDate = feeitem["start_day"] = feeitem["end_day"] = item.date;

        //   8. 7/31 / 8/1~8/31
        if (feeitem.kind == "") {
          createDate = dayjs(item.date)
            .add(-1, "month")
            .endOf("month")
            .format("YYYY-MM-DD");
          feeitem["start_day"] = firstDay;
          feeitem["end_day"] = lastDay;
        }
        if (feeitem.kind == "") {
          createDate = dayjs(item.date)
            .add(-1, "month")
            .startOf("month")
            .format("YYYY-MM-DD");
        }

        postData.push({
          user_snkey: this.selectUser.snkey,
          fee_snkey: feeitem.snkey,
          date: createDate,
          start_day: feeitem.start_day,
          end_day: feeitem.end_day,
          quantity: item.quantity,
          price: feeitem.price,
          note: item.note,
          create_man:
            this.$store.state.pData.username +
            "(" +
            this.$store.state.time +
            ")", //,,
        });

        // -> 
        if (rsData.newUser && feeitem.kind == "") {
          const feeDay = dayjs(item.date).add(-1, "day").format("DD");
          const newFee =
            parseInt(feeitem.price) / parseInt(dayjs(lastDay).format("DD"));

          postData.push({
            user_snkey: this.selectUser.snkey,
            fee_snkey: feeitem.snkey,
            date: createDate,
            start_day: dayjs(feeitem.start_day).format("YYYY-MM-DD"),
            end_day: dayjs(item.date).add(-1, "day").format("YYYY-MM-DD"),
            quantity: item.quantity,
            price: -(feeDay * Math.floor(newFee)).toFixed(0),
            note: `(A) ${dayjs(feeitem.start_day).format(
              "MM/DD"
            )}~${dayjs(item.date)
              .add(-1, "day")
              .format("MM/DD")} (${feeDay} )`,
            create_man:
              this.$store.state.pData.username +
              "(" +
              this.$store.state.time +
              ")", //,,
          });
        }
      });

      return postData;
    },
    /*  */
    postAccountingData(postData) {
      /*2020/11/15 "",26,1 */

      /* 26() */
      /*
     promise fee,getfeeItems.then(function (feeItems)=>{}) 
     feeItemsgetfeeItemsrs.data
     */
      //step1
      let getfeeItems = new Promise((resolve, reject) => {
        this.axios
          .post("general/getAll/" + this.$store.state.databaseName + "/fee")
          .then((rs) => {
            // const feeItems = rs.data;
            resolve(rs.data);
          })
          .catch((err) => {
            reject(err);
          });
      });

      //step2
      var vm = this; //vmthis,promisethis
      getfeeItems.then(function (feeItems) {
        postData = postData.filter((accountingItem) => {
          if (dayjs(accountingItem.date).format("DD") >= 26) {
            let rs = feeItems.find((item) => {
              return item.snkey == accountingItem.fee_snkey;
            });
            if (rs.kind != "") {
              accountingItem.note = accountingItem.date + '' + dayjs(accountingItem.date).add(1, "month").format("YYYY-MM-01");
              accountingItem.date = dayjs(accountingItem.date).add(1, "month").format("YYYY-MM-01");
            }
          }
          return accountingItem;
        });

        vm.axios
          .post(
            "general/addMulti/" + vm.$store.state.databaseName + "/accounting",
            qs.stringify(postData)
          )
          .then((rs) => {
            if (rs.data.state == 1) {
              var pop = { msg: "", type: true, theme: "success" };
              vm.$store.commit("snackbar", pop);
              vm.getAccounting();
            }
          });
      });
      /* 26 end*/

      /* , ->  */
      // this.axios
      //   .post(
      //     "general/addMulti/" + this.$store.state.databaseName + "/accounting",
      //     qs.stringify(postData)
      //   )
      //   .then((rs) => {
      //     if (rs.data.state == 1) {
      //       var pop = { msg: "", type: true, theme: "success" };
      //       this.$store.commit("snackbar", pop);
      //       this.getAccounting();
      //     }
      //   });
      /* , end*/
    },
    finishAdd(item) {
      var postData = this.rebuildDay(item);
      this.postAccountingData(postData);
      
    },
    finishEdit(item) {
      //(popupadd,,itemlist)
      this.$store.commit("getTime"); //
      var postData = {
        date: item.date,
        snkey: item.snkey,
        start_day: item.start_day,
        end_day: item.end_day,
        quantity: item.quantity,
        price: item.price,
        note: item.note,
        edit_man:
          this.$store.state.pData.username + "(" + this.$store.state.time + ")", //,,
      };

      this.axios
        .post(
          "general/edit/" + this.$store.state.databaseName + "/accounting",
          qs.stringify(postData)
        )
        .then((rs) => {
          if (rs.data.state == 1) {
            var pop = { msg: "", type: true, theme: "success" };
            this.$store.commit("snackbar", pop);
            //this.getAccounting();
            this.computeMonthTotal();
          }
        });
    },
    finishDel(delData) {
      //
      this.$confirm("?").then((res) => {
        if (res) {
          delData.delman =
            this.$store.state.pData.username +
            "(" +
            dayjs().format("YYYY-MM-DD HH:mm:ss") +
            ")";
          var postData = {
            snkey: delData.snkey,
            tablename: "accounting",
            info: JSON.stringify(delData),
          };

          this.axios
            .post(
              "general/delv3/" + this.$store.state.databaseName + "/accounting",
              qs.stringify(postData)
            )
            .then((rs) => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                // this.getAllData();
                this.getAccounting();
              }
            });
        }
      });
    },
    // & 
    leaveOut() {
      if (this.selectUser.out_date == "0000-00-00") {
        var pop = { msg: "", type: true, theme: "warning" };
        this.$store.commit("snackbar", pop);
        return false;
      }

      // -->  -> ,
      this.$confirm("?").then((res) => {
        if (res) {
          const searchKey = dayjs(this.selectUser.out_date)
            .add(-1, "month")
            .endOf("month")
            .format("YYYY-MM-DD");
          let matchData = [...this.accountingItems];
          matchData = matchData.filter((i) => {
            return (
              i.date.includes(searchKey) &&
              i.kind == "" &&
              parseInt(i.price) > 0
            );
          });

          const startDay = dayjs(this.selectUser.out_date).format("MM/DD");
          const endDay = dayjs(this.selectUser.out_date)
            .endOf("month")
            .format("MM/DD");
          const FeeDays =
            dayjs(this.selectUser.out_date).endOf("month").format("DD") -
            dayjs(this.selectUser.out_date).add(-1, "day").format("DD"); //
          console.log(startDay, endDay, FeeDays);

          let postData = [];
          matchData.filter((feeitem) => {
            const newFee =
              parseInt(feeitem.price) / parseInt(dayjs(endDay).format("DD"));
            // console.log("newFee", newFee);
            postData.push({
              user_snkey: feeitem.user_snkey,
              fee_snkey: feeitem.fee_snkey,
              date: dayjs(this.selectUser.out_date).format("YYYY-MM-DD"),
              start_day: dayjs(this.selectUser.out_date).format("YYYY-MM-DD"),
              end_day: dayjs(this.selectUser.out_date)
                .endOf("month")
                .format("YYYY-MM-DD"),
              quantity: feeitem.quantity,
              price: -(FeeDays * Math.floor(newFee)).toFixed(0),
              note: `(A) ${startDay}~${endDay} ${FeeDays})`,
              create_man:
                this.$store.state.pData.username +
                "(" +
                this.$store.state.time +
                ")", //,,
            });
          });
          this.postAccountingData(postData); //

          //
          postData = {
            snkey: this.selectUser.snkey,
            out_date: this.selectUser.out_date,
            edit_man: "",
          };

          //this.$store.commit("setLoadingOn"); //on
          this.axios
            .post(
              "general/edit/" + this.$store.state.databaseName + "/user",
              qs.stringify(postData)
            )
            .then((rs) => {
              // console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                // this.$store.commit("setLoadingOff"); //off
              }
            });
          //end
        }
      });
    },
    updateUser() {
      // & 
      var postData = {
        snkey: this.selectUser.snkey,
        promise_fee: this.selectUser.promise_fee,
        margin_date: this.selectUser.margin_date,
        margin: this.selectUser.margin,
        edit_man: "",
      };

      //this.$store.commit("setLoadingOn"); //on
      this.axios
        .post(
          "general/edit/" + this.$store.state.databaseName + "/user",
          qs.stringify(postData)
        )
        .then((rs) => {
          // console.log(rs.data);
          if (rs.data.state == 1) {
            var pop = { msg: "", type: true, theme: "success" };
            this.$store.commit("snackbar", pop);
            // this.$store.commit("setLoadingOff"); //off
          }
        });
    },
    finishEditPayment(item) {
      //(popupadd,,itemlist)
      this.$store.commit("getTime"); //
      item["edit_man"] =
        this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //,,

      this.axios
        .post(
          "general/edit/" + this.$store.state.databaseName + "/payment",
          qs.stringify(item)
        )
        .then((rs) => {
          if (rs.data.state == 1) {
            var pop = { msg: "", type: true, theme: "success" };
            this.$store.commit("snackbar", pop);
            // this.computeMonthTotal();
          }
        });
    },
  },
};
</script>


